@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    position: relative;
    background-color: #f0f3f5;
}

#mainMenu {
    height: calc(100vh - 24px);
}

#backdrop {
    visibility: hidden;
    background-color: #141f33;
    opacity: 0;
}

#mainMenu:hover+#backdrop {
    visibility: visible;
    opacity: .7;
}